html {
  box-sizing: border-box;
  font-size: 0.5208333333vw;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  display: flex;
  flex-direction: column;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  flex-grow: 1;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main {
  flex-grow: 1;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: 1.5rem;
  font-weight: 400;
  font-size: 3.6rem;
  line-height: 1;
  text-transform: uppercase;
  color: var(--cvet-osnova);
  padding: 3rem 6rem;
  border: 1px solid #000;
  transition: color 0.3s ease-out;
  overflow: hidden;
}
.btn span {
  position: relative;
  z-index: 3;
}
.btn svg {
  width: 13rem;
  height: auto;
  fill: #000;
  transition: fill 0.3s ease-out;
}
.btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--cvet-osnova);
  transform: translateX(-100%);
  transition: transform 0.3s ease-out, background-color 0.3s ease-out;
}
.btn:hover::before {
  transform: translateX(0);
}
.btn:hover span {
  color: #fff;
  transition: color 0.3s ease-out;
}
.btn:hover svg {
  position: relative;
  z-index: 3;
  fill: #fff !important;
}
.btn:active::before {
  background-color: #17241d;
}

.h2 {
  margin: 0;
  font-family: var(--font7);
  font-weight: 400;
  font-size: 5rem;
  line-height: 110%;
  text-transform: uppercase;
  text-align: center;
  color: #1f1f1f;
}

.title3 {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 5rem;
  line-height: 120%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--cvet-osnova);
}

.second-screen {
  position: fixed;
  z-index: 90;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  clip-path: circle(25% at 25% 75%);
}

.wrappp {
  background-image: url("./../img/letters/bg.webp");
  background-position: center center;
  background-size: cover;
}

.highlite {
  color: #dfb57a;
}

.item-header {
  display: flex;
  align-items: center;
  margin-bottom: 4rem;
  gap: 4rem;
}
.item-header__back {
  width: 16.4rem;
}
.item-header__back svg {
  width: 100%;
}
.item-header__title {
  position: relative;
  display: flex;
  align-items: center;
  gap: 4rem;
  flex-grow: 1;
}
.item-header__title::after {
  content: "";
