  z-index: 5;
  left: 0;
  top: 100%;
  width: 100%;
  background-color: #fff;
  box-sizing: content-box;
  max-height: 0;
  overflow: hidden;
  will-change: max-height;
  transition: max-height 0.3s ease-out;
}
.select__item, .select__header {
  position: relative;
  width: 10.5rem;
