  justify-content: flex-end;
  padding: 0 2rem;
}
.select__item {
  justify-content: center;
}
.select__item:not(:last-child) {
  border-bottom: none;
}
.select__input {
  left: 0;
  top: 0;
  width: 100%;
  height: 80%;
  padding: 0;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1;
  text-transform: uppercase;
  color: var(--cvet-osnova);
}
.select__input.focus-visible {
  outline: none;
}

.articul {
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 120%;
  color: var(--cvet-osnova);
}
.articul:not(:last-child) {
  margin-bottom: 2.5rem;
}

.item-desc {
  padding: 2.5rem 3.5rem;
  border-radius: 1rem;
  background-color: #f1f5ee;
}
.item-desc__title {
  display: block;
  margin-bottom: 1.7rem;
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 3.2rem;
  line-height: 120%;
  color: var(--cvet-osnova);
}
.item-desc__desc {
  margin: 0;
  font-family: var(--second-family);
  font-weight: 300;
  font-size: 2.4rem;
  line-height: 120%;
  color: var(--cvet-osnova);
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

.gallery {
  min-width: 0;
  position: relative;
}
.gallery__slider {
  min-width: 0;
  max-width: 100%;
  margin-bottom: 0.3rem;
}
.gallery__slider > .swiper-wrapper > .swiper-slide {
  height: 69.5rem;
}
.gallery__slider > .swiper-wrapper > .swiper-slide img {
  display: block;
  height: 100%;
  width: 100%;
}
.gallery__thumbs > .swiper-wrapper > .swiper-slide {
  cursor: pointer;
  height: 18rem;
}
.gallery__thumbs > .swiper-wrapper > .swiper-slide img {
  display: block;
  height: 100%;
  width: 100%;
}

.breadcrumbs > ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: flex-end;
  gap: 0.4rem;
}
.breadcrumbs > ul > li {
  display: flex;
  align-items: flex-end;
  gap: 0.4rem;
}
.breadcrumbs > ul > li:not(:last-child)::after {
  content: "";
  display: block;
  width: 2rem;
  height: 2rem;
  background-image: url("./../img/arrow-right.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
.breadcrumbs > ul > li > a {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 120%;
  color: var(--cvet-osnova);
}

.recommend {
  padding: 10rem 0;
  background: #fff;
}
.recommend--similar {
  background: linear-gradient(180deg, #fff 0%, #eff3ea 100%);
}
.recommend__title {
  font-size: 3rem;
  margin-bottom: 7rem;
  display: flex;
  align-items: center;
  gap: 4rem;
}
.recommend__title::after {
  content: "";
  display: block;
  height: 0.2rem;
  flex-grow: 1;
  background-color: var(--cvet-osnova);
}
.recommend__title--end {
  flex-direction: row-reverse;
}
.recommend__items {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 3rem;
}
.recommend__item {
  height: unset;
  min-height: 30rem;
}
.recommend__item .catalogue-item__image {
  height: 27rem;
}
.recommend__item .catalogue-item__name {
  font-size: 1.6rem;
}
.recommend__item .catalogue-item__link {
  padding: 1.8rem;
}
@media (any-hover: hover) {
  .recommend__item:hover .catalogue-item__image {
    height: 19rem;
  }
}

.socials {
  padding: 6rem 0;
}
.socials__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  margin-bottom: 2rem;
}
.socials__item {
  height: 43.3rem;
}
.socials__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.socials__desc {
  margin: 0;
  max-width: 60.8rem;
  font-family: var(--font4);
  font-weight: 300;
  font-size: 2rem;
  line-height: 175%;
  color: #425c4c;
}

.favorites-section {
  padding-top: 20rem;
  padding-bottom: 10rem;
}
.favorites-section__items {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 6rem 3rem;
}
.favorites-section__buttons {
  margin-top: 6rem;
  margin-bottom: 6rem;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 2rem;
}
.favorites-section__btn--clear {
  font-weight: 400;
  font-size: 2rem;
  line-height: 120%;
  text-transform: uppercase;
  color: var(--cvet-osnova);
}
.favorites-section__btn--add {
  padding: 1.8rem 8rem;
  font-size: 2rem;
}
.favorites-section__btn--add svg {
  width: 3.7rem;
}

.added-item__image {
  height: 33rem;
  margin-bottom: 0.6rem;
}
.added-item__image img {
  height: 100%;
  width: 100%;
}
.added-item__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
.added-item__title {
  display: block;
  font-family: var(--second-family);
  font-weight: 300;
  font-size: 1.6rem;
  line-height: 120%;
  color: var(--cvet-osnova);
}
.added-item__buttons {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}
.added-item__select .select__header {
  width: auto;
  height: 2.6rem;
  gap: 0.8rem;
  padding: 0.4rem 1rem;
}
.added-item__select .select__icon {
  width: 1.8rem;
  height: 1.8rem;
  flex-shrink: 0;
}
.added-item__select .select__icon svg {
  width: 100%;
  height: 100%;
}
.added-item__select .select__input {
  height: 100%;
  padding: 0;
}
.added-item__price {
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 120%;
  text-align: center;
  color: var(--cvet-osnova);
}

.order-section {
  padding-top: 20rem;
  padding-bottom: 4rem;
}

.order-form {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem 4rem;
}
.order-form__title {
  grid-column: 4 span;
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 3.6rem;
  line-height: 97%;
  color: #425c4c;
}
.order-form__label {
  display: block;
  width: 100%;
  min-height: 5.6rem;
  border: 1px solid #e2e2e2;
}
.order-form__label--long {
  grid-column: 2 span;
}
.order-form__input {
  width: 100%;
  height: 100%;
  background: none;
  border: none;
  font-weight: 400;
  font-size: 1.8rem;
  text-transform: uppercase;
  color: rgba(43, 43, 43, 0.6);
  padding: 1.7rem 2rem;
}
.order-form__input.focus-visible {
  outline: none;
}
.order-form__area {
  grid-column: 2 span;
  grid-row: 3 span;
  border: 1px solid #e2e2e2;
  padding: 1.7rem 2rem;
  font-weight: 400;
  font-size: 1.8rem;
  text-transform: uppercase;
  color: rgba(43, 43, 43, 0.6);
}
.order-form__total {
  margin-top: 4rem;
  grid-column: 4 span;
}
.order-form__agree {
  margin-top: 2rem;
  grid-column: 4 span;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.order-form__btn {
  display: flex;
  width: 100%;
  justify-content: center;
  padding: 1.8rem;
  margin-bottom: 2rem;
}
.order-form__btn span {
  line-height: 1;
  font-size: 2rem;
}
.order-form__btn svg {
  width: 3.7rem;
}
.order-form__btns {
  width: 100%;
  margin: 0 auto;
  max-width: 63rem;
}
.order-form__check:checked + .order-form__rcheck > span {
  background-color: blue;
}
.order-form__rcheck {
  display: flex;
  align-items: center;
  gap: 2rem;
  cursor: pointer;
}
.order-form__rcheck > span {
  flex-shrink: 0;
  display: block;
  width: 2.8rem;
  height: 2.8rem;
  background-color: #fff;
  border: 1px solid #000;
}
.order-form__rcheck > p {
  margin: 0;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 120%;
  color: rgba(39, 39, 39, 0.6);
}

.order-total {
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 2.6rem;
  line-height: 135%;
}
.order-total__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.order-total__capture {
  color: #425c4c;
  flex-grow: 1;
  overflow: hidden;
  position: relative;
}
.order-total__capture::after {
  content: "...............................................................................................................................................................................................................................................................................................................................";
  position: absolute;
  color: rgba(66, 92, 76, 0.2);
}
.order-total__price {
  color: #000;
}

.incart {
  padding-top: 4rem;
}
.incart__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.incart__title {
  margin: 0;
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 3.6rem;
  line-height: 110%;
  letter-spacing: 0.03em;
  color: var(--cvet-osnova);
}
.incart__slider {
  min-width: 0;
  max-width: 100%;
}
.incart__controls {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.incart__btn {
  width: 4rem;
}
.incart__btn.swiper-button-disabled {
  opacity: 0.5;
  pointer-events: none;
}
.incart__btn svg {
  width: 100%;
  height: auto;
  fill: #000;
}

.filters {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  min-height: 49rem;
  background-color: #fff;
  padding: 7rem 4.3rem;
  transform: translateY(-100%);
  z-index: -1;
  transition: transform 0.5s ease-out, z-index 0.5s ease-out;
}
.filters.active {
  transform: translateY(0);
  z-index: 9999;
}
.filters__close {
  position: absolute;
  z-index: 8;
  right: 2.5rem;
  top: 2.5rem;
  width: 2.4rem;
  height: 2.4rem;
}
.filters__close img {
  width: 100%;
  height: 100%;
}
.filters__btn {
  padding: 2rem 5rem;
  height: 6rem;
}
.filters__btn span {
  font-size: 2rem;
}
.filters__btn.active span {
  color: #fff;
}
.filters__btn.active::before {
  transform: translateX(0);
}
.filters__header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
  margin-bottom: 4.5rem;
}
.filters__row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}
.filters__row:not(:last-child) {
  margin-bottom: 2.5rem;
}
.filters__block:not(:last-child) {
  margin-bottom: 4.5rem;
}
.filters__content {
  display: none;
}
.filters__content.active {
  display: block;
}