.pack-turn {
  padding-top: 5rem;
  padding-bottom: 28rem;

  &__container {
    display: flex;
    padding-left: 8rem;
    position: relative;
  }

  &__content {
    display: flex;
    align-items: flex-start;
    margin-right: 6rem;
    position: relative;
  }

  &__text {
    width: 100%;
    max-width: 60rem;
  }



  &__left {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-right: 13rem;
    transform: translateY(25rem);

    .pack-turn {
      &__img {
        background-color: #000;

        &:nth-child(1) {
          position: relative;
          z-index: 3;
          width: 27.4rem;
          height: 25.3rem;
          margin-left: auto;
          margin-bottom: -5rem;
          margin-right: -8.3rem;

          &::before {
            content: "";
            position: absolute;
            z-index: -1;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background-image: url('./../img/backgrounds/green.webp');
            background-position: center center;
            background-size: cover;
            background-repeat: no-repeat;
            transform: scale(1.12);
          }
        }

        &:nth-child(2) {
          width: 32.1rem;
          height: 38.1rem;
          position: relative;

          &::before {
            content: "";
            position: absolute;
            z-index: -1;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: #000;
            transform: scale(1.06);
          }
        }

        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  &__figure {
    left: 16rem;
  }

  &__image {
    width: 100%;
    max-width: 80rem;
    height: 110rem;
    position: absolute;
    z-index: -1;
    right: -3rem;
    top: 50%;
    transform: translateY(-50%);

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}